#portfolio {
	margin-bottom: 150px;

	.inner {
		width: 1180px;
		margin: 0 auto;

		&::after {
			content: '';
			display: block;
			clear: both;
		}

		.sub_tit {
			width: 25%;
			float: left;
			padding: 25px 0;
			box-sizing: border-box;

			h1 {
				font: 500 30px/1 'Montserrat';
				color: #666;
				text-align: left;
				position: relative;
				margin-bottom: 15px;

				&::after {
					content: '';
					display: block;
					width: 190px;
					height: 5px;
					transition: 0.5s;
					position: absolute;
					bottom: 2px;
					z-index: -1;
				}
			}

			p {
				font: 12px/1.2 'Montserrat';
				color: #aaa;
				text-align: left;
				margin-bottom: 30px;
			}

			.btn {
				display: block;
				width: 170px;
				height: 45px;
				text-align: center;
				font: 500 12px/45px 'Roboto';
				color: #fff;
				box-shadow: 2px 5px 15px #eee;
				transition: 0.5s;
			}
		}
	}
}
